/**
 * = Cards
 */

.card {
  border-top: 2px solid white;
  border-left: 2px solid white;
  border-right: 2px solid #393939;
  border-bottom: 2px solid #393939;
  border-radius: 0;
  background: #c0c0c0;
}

.card-primary .card-header {
  background: #c0c0c0;
}

.card-secondary .card-header {
  background: #008081;
  color: white;
}

.card-tertiary .card-header {
  background: #000181;
  color: white;
}

.card-header {
  position: relative;
  background: #808080;
  font-size: 0.65rem;
  padding: 4px;
  color: #c9c9c9;
  border-radius: 0;
  border: 2px solid #bdbdbd;
}

.card-header .icon {
  position: absolute;
  top: -5px;
  left: -3px;
}

.card-body {
  padding: 10px 7px;
  border-top: 2px solid #bdbdbd;
  border-left: 3px solid #bdbdbd;
  border-right: 3px solid #bdbdbd;
  border-bottom: 2px solid #bdbdbd;
}

.card-footer {
  padding: 10px 7px;
}

/**
 * = Close
 */

.close {
  transition: all 0.2s ease;
}

.close > span:not(.sr-only) {
  display: block;
  height: 1.25rem;
  width: 1.25rem;
  background-color: transparent;
  color: rgba(0, 0, 0, 0.6);
  line-height: 17px;
  border-radius: 50%;
  font-size: 1.25rem;
  transition: all 0.2s ease;
}

.close:hover,
.close:focus {
  background-color: transparent;
  color: rgba(0, 0, 0, 0.9);
  outline: none;
}

.close:hover span:not(.sr-only),
.close:focus span:not(.sr-only) {
  background-color: transparent;
}

/**
 * = Dropdowns
 */

.dropdown-menu {
  min-width: 12rem;
}

.dropdown-menu .dropdown-header,
.dropdown-menu .dropdown-item {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
}

.dropdown-menu .dropdown-header {
  color: #3e4555;
  font-weight: 500;
}

.dropdown-menu .dropdown-item {
  color: #697992;
  transition: all 0.2s ease;
  font-weight: 300;
}

.dropdown-menu .dropdown-itema:hover {
  color: #3e4555;
}

.show .dropdown-menu {
  animation: show-dropdown 0.2s ease forwards;
}

[data-toggle]:hover {
  cursor: pointer;
}

.dropdown-toggle:after,
.dropright .dropdown-toggle:after,
.dropleft .dropdown-toggle:before,
.dropup .dropdown-toggle:after {
  display: none;
}

.dropdown-menu-sm {
  min-width: 100px;
  border: 0.3rem;
}

.dropdown-menu-md {
  min-width: 180px;
  border: 0.3rem;
}

.dropdown-menu-lg {
  min-width: 260px;
  border-radius: 0.3rem;
}

.dropdown-menu-xl {
  min-width: 450px;
  border-radius: 0.3rem;
}

/**
 * = Forms
 */

label {
  font-size: 0.6rem;
  color: black;
  margin: 0;
}

.form-group {
  position: relative;
}

.form-control,
.form-control:focus {
  font-size: 0.6rem;
  color: black;
  padding: 0.3rem;
  border-color: #4a4a4a;
  box-shadow: none;
  border-radius: 0;
}

.form-group-input {
  position: relative;
}

.form-group-input:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.form-group-input:before {
  top: 0;
  width: 100%;
  height: 1px;
  background: #000000;
}

.form-group-input:after {
  border-left: 1px solid #000000;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #dfdfdf;
  border-right: 1px solid #dfdfdf;
}

.form-control.is-valid:focus,
.form-control.is-invalid:focus {
  box-shadow: none;
}

::selection {
  background-color: #0004bd;
  color: white;
}

/* Textareas */

textarea[resize="none"] {
  resize: none !important;
}

textarea[resize="both"] {
  resize: both !important;
}

textarea[resize="vertical"] {
  resize: vertical !important;
}

textarea[resize="horizontal"] {
  resize: horizontal !important;
}

.form-control-lg {
  font-size: 1rem;
}

.form-control-xl {
  height: calc(2.25em + 1.75rem + 0.0625rem);
  padding: 0.875rem 1rem;
  line-height: 1.5;
  font-size: 1.25rem;
  border-radius: 0.3rem;
}

.shadow-group-success,
.shadow-group-danger {
  box-shadow: none;
}

.has-danger .form-control::-ms-input-placeholder {
  color: #fa5252;
}

.has-danger .form-control::placeholder {
  color: #fa5252;
}

.has-success .form-control::-ms-input-placeholder {
  color: #00bf9a;
}

.has-success .form-control::placeholder {
  color: #00bf9a;
}

.form-check {
  margin: 0;
  padding-left: 0;
}

.form-check .form-check-label {
  display: inline-block;
  position: relative;
  padding-left: 1.75rem;
  font-weight: 400;
  line-height: 16px;
  margin-bottom: 0;
  color: black;
}

.form-check-x {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  z-index: 9;
}

.form-check-x:before,
.form-check-x:after {
  content: "";
  display: block;
  position: absolute;
  background: black;
  left: 49%;
  top: -1px;
  width: 1px;
  height: 17px;
}

.form-check-x:before {
  transform: rotate(135deg);
}

.form-check-x:after {
  transform: rotate(-135deg);
}

@media (prefers-reduced-motion: reduce) {
  .form-check .form-check-label {
    transition: none;
  }
}

.form-check .form-check-label:hover {
  cursor: pointer;
}

.radio .form-check-sign {
  padding-left: 28px;
}

.form-check-radio.form-check-inline .form-check-label {
  padding-left: 5px;
  margin-right: 10px;
}

.form-check .form-check-sign::before {
  content: "";
  display: inline-block;
  position: absolute;
  width: 15px;
  height: 15px;
  left: 0;
  top: 0;
  background-color: white;
  border: 1px solid #636363;
  border-radius: 0;
}

@media (prefers-reduced-motion: reduce) {
  .form-check .form-check-sign::before {
    transition: none;
  }
}

.form-check .form-check-sign::before:hover,
.form-check .form-check-sign::after:hover {
  cursor: pointer;
}

.form-check input[type="checkbox"]:checked + .form-check-sign::before {
  border: none;
}

.form-check.disabled .form-check-label {
  color: #adb5bd;
  cursor: not-allowed;
}

.form-check input[type="checkbox"],
.radio input[type="radio"] {
  opacity: 0;
  position: absolute;
  visibility: hidden;
}

.form-check input[type="checkbox"]:checked + .form-check-x {
  display: block;
}

.form-check input[type="checkbox"]:disabled + .form-check-sign::after {
  color: rgba(23, 79, 132, 0.7);
}

.form-check input[type="checkbox"] + .form-check-sign::after {
  opacity: 0;
}

.form-control input[type="checkbox"]:disabled + .form-check-sign::before,
.checkbox input[type="checkbox"]:disabled + .form-check-sign::after {
  cursor: not-allowed;
}

.form-check input[type="checkbox"]:disabled + .form-check-sign,
.form-check input[type="radio"]:disabled + .form-check-sign {
  pointer-events: none;
}

.form-check-radio .form-check-label {
  padding-top: 3px;
}

.form-check-radio .form-check-sign::before,
.form-check-radio .form-check-sign::after {
  content: " ";
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  left: 0px;
  top: 3px;
  padding: 1px;
}

@media (prefers-reduced-motion: reduce) {
  .form-check-radio .form-check-sign::before,
  .form-check-radio .form-check-sign::after {
    transition: none;
  }
}

.form-check-radio input[type="radio"] + .form-check-sign:after,
.form-check-radio input[type="radio"] {
  opacity: 0;
}

.form-check-radio input[type="radio"]:checked + .form-check-sign::after {
  width: 6px;
  height: 6px;
  top: 9px;
  left: 6px;
  background-color: black;
  border-color: black;
  opacity: 1;
}

.form-check-radio input[type="radio"]:checked + .form-check-sign::before {
  background-color: white;
}

.form-check-radio input[type="radio"]:checked + .form-check-sign::after {
  opacity: 1;
}

.form-check-radio input[type="radio"]:disabled + .form-check-sign::before {
  color: rgba(23, 79, 132, 0.7);
}

.form-check-radio input[type="radio"]:disabled + .form-check-sign::before,
.form-check-radio input[type="radio"]:disabled + .form-check-sign::after {
  color: #adb5bd;
}

.round-check .form-check-sign::before,
