.icon {
  text-align: center;
  display: inline-flex;
  -moz-align-items: center;
  -ms-align-items: center;
  align-items: center;
  -moz-justify-content: center;
  -ms-justify-content: center;
  justify-content: center;
  -ms-flex-pack: center;
}

.icon-xs {
  transform: scale(0.5);
}

.icon-sm {
  transform: scale(0.7);
}

.icon i,
.icon svg {
  font-size: 2.5rem;
}

.icon.icon-sm i,
.icon.icon-sm svg {
  font-size: 1.5rem;
}

.icon.icon-lg i,
.icon.icon-lg svg {
  font-size: 3rem;
}

.icon .organic-shape {
  margin-left: 0.5rem;
}

.icon.rounded-circle .icon-bordered {
  border-radius: 50%;
}

.icon.organic-radius .icon-bordered {
  border-radius: 63% 37% 30% 70%/50% 45% 55% 50%;
}

/**
 * = Images
 */

.image-lg {
  height: 12rem;
}

.image-md {
  height: 5rem;
}

.image-sm {
  height: 3rem;
}

.img-thumbnail {
  border-width: 0.125rem;
  box-shadow: none;
}

.gallery-feed img {
  width: 20%;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  float: left;
}

@media (min-width: 576px) {
  .effect-img-2 {
    position: absolute;
    right: 5rem;
    top: 25%;
    z-index: 2;
    margin: 0;
  }
  .effect-img-1,
  .effect-img-2 {
    margin: 0 0 3rem;
    width: 350px;
    height: auto;
  }
}

@media (max-width: 991.98px) {
  .effect-img-2 {
    right: 0.425rem;
    top: 0;
  }
}

/**
 * = Input groups
 */

.input-group {
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.05);
  border-radius: 0.125rem;
  transition: all 0.2s ease;
}

.input-group .form-control {
  box-shadow: none;
}

.input-group .form-control:not(:first-child) {
  border-left: 0;
  padding-left: 0;
}

.input-group .form-control:not(:last-child) {
  border-right: 0;
  padding-right: 0;
}

.input-group .form-control:focus {
  box-shadow: none;
}

.input-group-text {
  font-size: 0.875rem;
  transition: all 0.3s ease-in-out;
}

.input-group-prepend {
  margin-right: -2px;
}

.focused .input-group-text {
  color: #869ab8;
  background-color: #ffffff;
}

.focused .input-group-shadow {
  box-shadow: 0.1rem 0.1rem 0 rgba(233, 236, 239, 0.5);
}

.language-html {
  color: white;
  background: #008081;
}

/**
 * = Maps
 */

.map {
  height: 400px;
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
}

