/**
 * = Navs
 */

.nav-link {
  color: #869ab8;
}

.nav-link:hover,
.nav-link.active {
  color: #c0c0c0;
}

.nav-link:hover img,
.nav-link.active img {
  opacity: inherit;
  transition: all 0.2s ease;
}

.nav-link i {
  position: relative;
  margin-right: 0.5rem;
}

.nav-link img {
  opacity: 0.5;
}

.tab-content {
  background: #c0c0c0;
  padding: 1rem 1.4rem;
}

.nav-tabs {
  border-bottom: 2px solid #dededf;
}

.nav-tabs .nav-item {
  position: relative;
  margin-bottom: 0;
  background: #c0c0c0;
  color: black;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-right: 1px solid #5a5a5a;
  box-sizing: border-box;
}

.nav-tabs .nav-item .nav-link {
  font-size: 0.5rem;
  color: black;
  padding: 0.2rem 1.8rem;
  font-style: italic;
  box-sizing: border-box;
  transform: none;
}

.nav-tabs .nav-item .nav-link.active {
  position: relative;
  background: #c0c0c0;
  border-right: 1px solid #5a5a5a;
}

.nav-tabs .nav-item:not(:first-child) .nav-link.active {
  border-left: 1px solid #5a5a5a;
}

.nav-tabs .nav-item .nav-link.active:after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  position: absolute;
  left: 0;
  bottom: -2px;
  background: #c0c0c0;
}

.nav-tabs .nav-link {
  border: 0;
  padding: 1rem 1rem;
}

